import boardsPageTemp from './boardsPage.hbs';
import BaseView from '../baseView';
import BasePage from '../basePage/basePage';
import EventBus from '../../modules/eventBus';
import { BoardsActions, Events } from '../../modules/actions';
import Dispatcher from '../../modules/dispatcher';
/**
 * ,    
 */
export default new (class BoardsPage extends BaseView {
    /**
     * @constructor
     */
    constructor() {
        super([
            {
                type: 'click',
                className: 'toggle__block',
                func: () => {
                    BasePage.toggleLeft();
                },
            },
            {
                type: 'click',
                className: 'createDeskButtonUnic',
                func: (e) => {
                    const createDeskBg = document.getElementsByClassName('createDesk__bg')[0]; //   
                    const createDesk = document.getElementsByClassName('createDesk')[0]; //  
                    e.preventDefault(); //    
                    createDeskBg.classList.add('active'); //   'active'  
                    createDesk.classList.add('active'); //    ;
                },
            },
        ]);
        EventBus.subscribe(Events.boardsUpdate, this.onUpdate.bind(this));
    }
    /**
     *     View
     */
    render() {
        BasePage.render();
        this.onUpdate();
        Dispatcher.dispatch({
            type: BoardsActions.loadBoards,
        });
    }
    /**
     * ,     
     * @param {object} data  
     */
    onUpdate(data = { boards: undefined }) {
        this.removeListeners();
        const root = document.getElementById('root');
        root.innerHTML = boardsPageTemp({
            pageStatus: BasePage.pageStatus,
            boards: data.boards,
        });
        this._createListeners();
    }
});
