/**
 * ,     flux-
 */
class Dispatcher {
    /**
     * @constructor
     */
    constructor() {
        this._callbacks = new Map(); // ,           
        this._i = 0; //     
    }
    /**
     * ,    
     * @param {DispatcherCallback} callback ,    
     * @return {number} id  
     */
    register(callback) {
        this._callbacks.set(this._i, {
            action: callback,
            isWait: false,
        });
        return this._i++;
    }
    /**
     * ,    
     * @param {DispatcherAction} action ,    
     */
    dispatch(action) {
        this._currentAction = action;
        this._callbacks.forEach((el) => {
            el.isWait = true;
        });
        this._callbacks.forEach((el) => {
            if (el.isWait) {
                el.isWait = false;
                el.action(this._currentAction);
            }
        });
    }
    /**
     * ,    
     * @param {number} id ,    
     */
    unregister(id) {
        this._callbacks.delete(id);
    }
    /**
     * ,  ,       id
     * @param {Array<number>} ids ,    
     */
    wait(ids) {
        ids.map((id) => {
            const el = this._callbacks.get(id);
            if (el !== undefined && el.isWait) {
                el.isWait = false;
                el.action(this._currentAction);
            }
        });
    }
}
export default new Dispatcher();
